@import "../../styles/variables";

.footer {
  background-color: $back-dark;
  color: $text-dark;
  height: 15vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16;
  padding-top: 200px;
  padding-bottom: 50px;
  &.dark {
    background-color: $back-light;
    color: $text-light;
  }
}

.socialIcon {
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2em;
  position: relative;
  top: -150px;
  width: 80px;
  height: 80px;
  backdrop-filter: blur(20px);
  transition: 0.5s;
  margin: 0 10px;
  border-radius: 100px;
  overflow: hidden;
  color: rgba($back-light, 0.8);
  border: 1px solid rgba($back-light, 0.8);
  &.dark {
    backdrop-filter: blur(5px);
    color: rgba($back-dark, 1);
    border: 1px solid rgba($back-dark, 1);
  }
  &:hover {
    transform: translateY(-20px);
    color: $orange-color;
    border: 2px solid rgba($orange-color, 0.5);
    transition: 0.3s;
  }
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 120px;
    height: 100%;
    background-color: rgba($orange-color, 0.2);
    transform: skewX(45deg) translateX(150px);
    transition: 0.5s;
  }
}

.socialIcon:hover::before {
  transform: skewX(45deg) translateX(-150px);
}

@media only screen and (max-width: 450px) {
  .footer {
    padding-top: 75px;
  }
  .socialIcon {
   top: -120px;
  }
}
